.covid_info{
    /* padding:50px 0px; */
    width: 100%;
    min-height: 100vh;
    background-color:rgba(255, 255, 255, 0.1);
    background-position: fixed;
    background-size: cover;
    display: flex;
    justify-content:center;
    align-items:center;
}
.covid_info .covid_info_content{
    position: relative;
    width: 90%;
    min-height: 80vh;
    display: flex;
    justify-content:center;
    align-items:center;
}
.covid_info .covid_info_content .covid_info_box{
    max-width: 100%;
    color: rgb(0, 0, 0);
}

.covid_info_box h1,h3{
    margin: 10px;
    text-align:left;
    font-weight:400;
    color:rgb(153, 71, 3);
}
.covid_info_box h1{
    margin-top: 50px;
    text-align:center;
}
.covid_info_box p{
    text-indent:50px;
    text-align: justify;
    letter-spacing: 1px;
    margin:10px;
}
.covid_info_box b{
    font-weight:500;
    color:rgb(153, 71, 3);
}
.covidinfoimg{
    position:relative;
    margin:10px 0px;
    width:100%;
    min-height:200px;
    display: flex;
    justify-content:center;
    align-items:center;
    flex-wrap:wrap;
}
.covidinfoimg img{
    box-shadow: 0px 0px 2px rgba(146, 146, 146, 0.972);
    margin:10px;
    height:200px;
    width:200px;
    border-radius: 5px;
}
@media screen and (max-width:600px){
    .covidinfoimg img{
        width:80%;
    }
    .covid_info_box h1,h3{
        margin: 20px;
        text-align:left;
        font-weight:400;
       
        color:rgb(153, 71, 3);
    }
}
@media screen and (min-width:600px) and (max-width:1200px){
    .covidinfoimg img{
        height:250px;
        width:40%;
    }
}
@media screen and (min-width:1200px){
    .covidinfoimg img{
        height:250px;
        width:30%;
    }
}

